import http from 'src/utils/http';

export function getRoleList(params) {
  return http.post(`/role/roles`, params);
}
export function deleteRole(roleId) {
  return http.delete(`/role/${roleId}`);
}
export function getPrivileges(params) {
  return http.post(`/role/privileges`, params);
}
export function addRole(params) {
  return http.post(`/role`, params);
}
export function getMemberList(roleId, params) {
  return http.post(`/role/${roleId}/users`, params);
}
export function deleteMember(roleId, users) {
  return http.delete(`/role/${roleId}/user`, { data: users });
}
export function addMember(roleId, users) {
  return http.post(`role/${roleId}/user`, users);
}
export function getRolePrivilege(roleId) {
  return http.get(`/role/${roleId}/privileges`);
}
export function changePrivilege({params, roleId}) {
  return http.put(`/role/${roleId}/privileges`, params);
}
export function changeRoleStatus({roleId, status}) {
  return http.put(`/role/${roleId}/${status}`);
}
export function getAllMemberList(roleId, params) {
  return http.post(`/role/users/${roleId}`, params);
}



