<template>
  <div>
    <gs-button
      type="primary"
      @click="openAddRoleModal"
    >新建角色</gs-button>
    <gs-modal
      :title="title"
      v-model="visible"
      @visible-change="visibleChange"
      @confirm="addRole"
    >
      <gs-form
        ref="form"
        :model="form"
        :rules="rules"
        label-width="80px"
      >
        <gs-form-item
          label="角色名称"
          prop="roleName"
        >
          <gs-input v-model="form.roleName" />
        </gs-form-item>
        <gs-form-item
          label="描述"
          prop="description"
        >
          <gs-textarea v-model="form.description" />
        </gs-form-item>
        <gs-form-item
          label="技术方案"
          prop="privilegeId"
        >
          <gs-select
            v-model="form.privilegeId"
            class="doc-select"
            searchable
            popper-class="searchable-select"
          >
            <gs-option
              v-for="item in privilegesOptions"
              :key="item.privilegeId"
              :label="item.privilegeName"
              :value="item.privilegeId"
            />
          </gs-select>
        </gs-form-item>
      </gs-form>
    </gs-modal>
  </div>
</template>
<script>
import * as service from 'src/service/auth/role';
export default {
  data() {
    return {
      visible: false,
      form: {
        roleName: '',
        description: '',
        privilegeId: ''
      },
      privilegesOptions: [],
      rules: {
        roleName: [{ required: true, message: '请填写角色名称', trigger: 'blur' }],
        privilegeId: [{ required: true, message: '请选择技术方案', trigger: 'change' }]
      }
    };
  },
  computed: {
    title() {
      return '新建角色';
    }
  },
  methods: {
    openAddRoleModal() {
      this.visible = true;
    },
    addRole() {
      this.$refs.form.validate(valid => {
        if (valid) {
          service.addRole(this.form).then(data => {
            this.visible = false;
            this.$Message.success(`添加成功`);
            this.$emit('refreshRoleList');
          }).catch(e => {
          });
        }
      });
    },
    visibleChange(val) {
      if (val) {
        this.$refs.form.clearValidate();
        service.getPrivileges({
          limit: -1,
          query: '',
          offset: 0
        }).then(data => {
          this.privilegesOptions = data.lists;
        });
      }
    }
  }
};
</script>




