<template>
  <div class="role-mangement">
    <div class="search-box">
      <add-role
        @refreshRoleList="fetchRoleList"
      />
      <gs-search
        v-model="keyword"
        placeholder="请输入角色名称"
        @clear="fetchRoleList(1)"
        @search="fetchRoleList"
      />
    </div>
    <gs-table
      v-loading="loading"
      :data="tableData"
      :pagination="pagination"
      style="width: 100%"
      paging
      @size-change="sizeChange"
      @page-change="pageChange"
    >
      <gs-table-column
        prop="roleName"
        label="角色名称"
        show-overflow-tooltip
      />
      <gs-table-column
        prop="description"
        label="描述"
        show-overflow-tooltip
      />
      <gs-table-column
        prop="members"
        label="人数"
      />
      <gs-table-column
        prop="privilegeName"
        label="权限方案"
        show-overflow-tooltip
      />
      <gs-table-column
        prop="updateTime"
        label="更新时间"
      />
      <gs-table-column
        label="操作"
        width="160"
        fixed="right"
      >
        <template slot-scope="scope">
          <gs-button
            type="text-primary"
            @click="checkDetail(scope)"
          >详情</gs-button>
          <gs-button
            v-if="!!(scope.row.status)"
            :disabled="!!(!scope.row.disable)"
            type="text-primary"
            @click="changeRoleStatus(scope)"
          >禁用</gs-button>
          <gs-button
            v-if="!!(!scope.row.status)"
            :disabled="!!(!scope.row.disable)"
            type="text-primary"
            @click="changeRoleStatus(scope)"
          >启用</gs-button>
          <gs-button
            :disabled="!!(!scope.row.delete)"
            type="text-primary"
            @click="deleteRole(scope)"
          >删除</gs-button>
        </template>
      </gs-table-column>
    </gs-table>
  </div>
</template>
<script>
import './style';
import addRole from './add-role';
import * as service from 'src/service/auth/role';
export default {
  components: {
    addRole
  },
  data() {
    return {
      keyword: '',
      tableData: [
      ],
      pagination: {
        total: 0,
        pageSize: 10,
        pageSizes: [10, 20, 30],
        current: 1
      },
      loading: false
    };
  },
  computed: {

  },

  created() {
    this.fetchRoleList(1);
  },

  methods: {
    fetchRoleList(isfirstPage = 0) {
      if (isfirstPage === 1) this.pagination.current = 1;
      this.loading = true;
      service.getRoleList({
        offset: (this.pagination.current - 1) * this.pagination.pageSize,
        limit: this.pagination.pageSize,
        query: this.keyword
      }).then(data => {
        data.lists.map(item => {
          item.description = item.description || '-';
          item.members = item.members || '0';
          item.privilegeName = item.privilegeName || '-';
          item.updateTime = item.updateTime || '-';
          return item;
        });
        this.tableData = data.lists;
        this.pagination.total = data.sum || 0;
        this.loading = false;
      }).catch(e => {
        this.loading = false;
      });
    },
    pageChange(current) {
      this.pagination.current = current;
      this.fetchRoleList();
    },
    sizeChange(size) {
      this.pagination.pageSize = size;
      this.fetchRoleList(1);
    },
    checkDetail({row}) {
      row.roleId && this.$router.push(`/main/auth/roleManagement/${row.roleId}`);
    },
    deleteRole({row}) {
      if (row.roleId) {
        this.$Modal.confirm({
          title: '警告',
          content: '是否删除该角色？删除后将无法恢复',
          onOk: () => {
            service.deleteRole(row.roleId).then(data => {
              this.$Message.success('删除角色成功');
              this.fetchRoleList();
            });
          }
        });
      }
    },
    changeRoleStatus({row}) {
      this.$Modal.confirm({
        title: '警告',
        content: `确定要${row.status === 0 ? '启用' : '禁用'}该角色吗？`,
        onOk: () => {
          service.changeRoleStatus({
            roleId: row.roleId,
            status: row.status === 0 ? 1 : 0
          }).then(data => {
            this.$Message.success(`${row.status === 0 ? '启用' : '禁用'}角色成功`);
            this.fetchRoleList();
          });
        }
      });
    }
  }
};
</script>



